import { Component, h, Prop, State, Host } from '@stencil/core';
import { ColDef, GridApi } from 'ag-grid-community';

/**
 * @category Data Tables
 * @description A static data table component for displaying read-only tabular data without interactive features. Ideal for simple data presentation and reports.
 */

@Component({
    tag: 'at-static-table',
    shadow: false,
})
export class AtStaticTable {
    /**
     * Data to go into the table
     */
    @Prop()
    table_data: {
        items: any[];
        total: number;
    };

    /**
     * Column definitions for the table
     */
    @Prop()
    col_defs: ColDef[];

    /**
     * Max number of items per page
     */
    @Prop()
    page_size: number = 10;

    /**
     * If true the column manager will not be added
     */
    @Prop()
    hide_column_manager: boolean;

    /**
     * If true, disables pagination on the table and shows all data at once.
     * Useful for server-side pagination where you want to control pagination externally.
     */
    @Prop()
    use_custom_pagination?: boolean = false;

    @State()
    agGrid: GridApi;

    tableEl: HTMLAtTableElement;

    @State()
    tableCreated: boolean = false;

    async componentDidLoad() {
        if (this.col_defs && !this.tableCreated) {
            this.agGrid = await this.tableEl.createGrid();
            this.tableCreated = true;
        }
    }
    async componentDidUpdate() {
        if (this.col_defs && !this.tableCreated) {
            this.agGrid = await this.tableEl.createGrid();
            this.tableCreated = true;
        }
    }

    render() {
        return (
            <Host>
                {!this.hide_column_manager && (
                    <at-table-actions ag_grid={this.agGrid}>
                        <at-column-manager
                            slot="column-manager"
                            col_defs={this.col_defs}
                        />
                    </at-table-actions>
                )}
                <at-table
                    ref={(el) => (this.tableEl = el)}
                    ag_grid={this.agGrid}
                    table_data={this.table_data}
                    col_defs={this.col_defs}
                    page_size={this.page_size}
                    use_custom_pagination={this.use_custom_pagination}
                    disable_auto_init={true}
                />
            </Host>
        );
    }
}
